import Vue from 'vue'
import Router from 'vue-router'
const  QianDao = r => require.ensure([], ()=>r(require('@/components/qiandao/QianDao.vue')))
const  MyRecord = r => require.ensure([], ()=>r(require('@/components/qiandao/MyRecord.vue')))
Vue.use(Router)
export default new Router({
  routes: [
    {
      path:'/',
      name:'QianDao',
      component:QianDao
    },
    {
      path:'/my-record',
      name:'MyRecord',
      component:MyRecord
    }
  ]
})



